import { DatePipe } from "@angular/common";
import { Component, OnInit } from "@angular/core";
import {
  AbstractControlOptions,
  FormBuilder,
  FormGroup,
  Validators,
} from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { IJobTitleByDeptVM } from "app/admin/models/setup/job-title";
import {
  ICourseAddEditVM,
  ICourseCategoryModel,
  ICourseGetVM,
  IDeptCourseAddEditVM,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { JobTitleService } from "app/admin/services/setup/job-title.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { MessagesService } from "app/shared/services/messages.service";
import { MatCheckboxChange } from "@angular/material/checkbox";
import { RadioButtonModel } from "app/shared/components/dynamic-form-field/radio-button/radio-button.component";
import { LanguageService } from "app/shared/services/language.service";
import { NgxSpinnerService } from "ngx-spinner";
import { CategoryData } from "app/admin/models/admin/course-request";
import { CategoriesService } from "app/admin/services/setup/categories.service";
import { MatDialog } from "@angular/material/dialog";
import { CategorieComponent } from "../../categrioes/categorie.component";
import { DiplomaComponent } from "../diploma/diploma.component";
import { forkJoin } from "rxjs";
import {
  defaultInputAppearance,
  InputAppearance,
} from "app/shared/enums/mat-theme.enum";

@Component({
  selector: "app-add-course",
  templateUrl: "./add-course.component.html",
  styleUrls: ["./add-course.component.scss"],
})
export class AddCourseComponent implements OnInit {
  functionId: number;
  courseId: number = 0;
  mandatoryJobTitles: IJobTitleByDeptVM[];
  recommendedJobTitles: IJobTitleByDeptVM[];
  mainCategories: ICourseCategoryModel[];
  subCategories: ICourseCategoryModel[];
  submitted: boolean = false;
  course: ICourseGetVM;
  imageChange: boolean = false;
  removeImage: number = 0;

  myForm!: FormGroup;
  eduCompId: number;
  currentDate = new Date();
  backBtnUrl: string;

  showAddDiplomaButton: true;
  showAddCategoryButton: true;
  reloadDiploma: boolean = false;

  appearance: InputAppearance = defaultInputAppearance;

  constructor(
    private courseServ: CourseService,
    private jobServ: JobTitleService,
    private categoriesService: CategoriesService,
    private ar: ActivatedRoute,
    private fb: FormBuilder,
    private msg: MessagesService,
    public fileService: UploadFileService,
    private spinner: NgxSpinnerService,
    private authserv: AuthService,
    private router: Router,
    private datePipe: DatePipe,
    public languageService: LanguageService,
    private dialog: MatDialog
  ) {
    forkJoin([
      this.authserv.chechAuthorization("categories"),
      this.authserv.chechAuthorization("diploma"),
    ]).subscribe((results: any) => {
      if (results[0].returnValue == 1) {
        this.showAddCategoryButton = results[0].returnString;
      }
      if (results[1].returnValue == 1) {
        this.showAddDiplomaButton = results[1].returnString;
      }
    });

    ar.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });

    ar.paramMap.subscribe((params) => {
      let isFromRequestPage = Number(params.get("isFromRequestPage"));
      // console.log(isFromRequestPage);
      this.backBtnUrl =
        isFromRequestPage == 1
          ? "/dashboard/setup/course/teaching-request"
          : "/dashboard/setup/course/index";

      let id = Number(params.get("courseId"));
      // get course in edit
      if (id) {
        this.courseId = id;
        spinner.show();
        this.courseServ
          .getCourseById<ICourseGetVM>(this.courseId)
          .subscribe((res) => {
            // console.log("getCourseById", res);
            if (res) {
              this.course = res;
              this.myForm.patchValue(this.course);
              this.FormCtrls.subCourse_attach?.setValue(
                this.course.attach_path
              );
              this.FormCtrls.category_ids?.setValue(
                this.course.course_categories.map(
                  (category) => category.educational_category_id
                )
              );
              this.FormCtrls.courseHours?.setValue(
                Math.floor(this.course.subCourseMinutes / 60)
              );
              this.FormCtrls.courseMinutes?.setValue(
                this.course.subCourseMinutes % 60
              );
              // put jobTitles of the same department in one array
              let depts = [];
              this.course.department?.forEach((d) => {
                let index = depts.findIndex((i) => i.id == d.id);
                if (index == -1) depts.push(d);
                else depts[index].jobTitle.push(d.jobTitle[0]);
              });
              this.course.department = depts;
              // console.log(this.course);

              let mandatoryDepartment = [];
              let recommendedDepartment = [];
              let mandatoryJobTitles = [];
              let recommendedJobTitles = [];
              this.course.department?.forEach((dept) => {
                dept.jobTitle?.forEach((job) => {
                  // mandatory
                  if (job.is_mandatory) {
                    mandatoryDepartment.push(dept.id);

                    mandatoryJobTitles.push(job.depJobId);
                    this.mandatoryDeptCtrl.setValue(mandatoryDepartment);
                    this.onSelectDepartment(job.is_mandatory);
                    this.mandatoryJobCtrl.setValue(mandatoryJobTitles);
                  } else {
                    //recommended
                    recommendedDepartment.push(dept.id);
                    recommendedJobTitles.push(job.depJobId);
                    this.recommendedDeptCtrl.setValue(recommendedDepartment);
                    this.onSelectDepartment(job.is_mandatory);
                    this.recommendedJobCtrl.setValue(recommendedJobTitles);
                  }
                });

                /* if (dept.is_mandatory == true) {
                  // department

                  mandatoryDepartment.push(dept.id);
                  this.mandatoryDeptCtrl.setValue(mandatoryDepartment);
                  this.onSelectDepartment(dept.is_mandatory);
                  // job titles
                  jobTitles = [];
                  dept.jobTitle?.forEach((job) => {
                    jobTitles.push(job.depJobId);
                  });
                  this.mandatoryJobCtrl.setValue(jobTitles);
                } else {
                  // department

                  recommendedDepartment.push(dept.id);
                  this.recommendedDeptCtrl.setValue(recommendedDepartment);
                  this.onSelectDepartment(dept.is_mandatory);
                  // job titles
                  jobTitles = [];
                  dept.jobTitle?.forEach((j) => jobTitles.push(j.depJobId));
                  this.recommendedJobCtrl.setValue(jobTitles);
                } */
              });

              if (isFromRequestPage == 1) {
                this.FormCtrls.isArabic.setValue(false);
                this.FormCtrls.isEnglish.setValue(false);
              }
              // console.log(this.myForm.value);
              spinner.hide();
            }
            // console.log(this.course);
          });
      }
    });

    this.eduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group(
      {
        subCourse_ar_name: ["", [Validators.required]],
        subCourse_en_name: ["", [Validators.required]],
        Diploma_id: [null],
        mainCategoryID: [null, [Validators.required]],
        subCategoryID: [
          {
            value: [],
            disabled: true,
          },
          [Validators.required],
        ],
        category_ids: [null, [Validators.required]],
        subjectPromoVideoPath: [""], //[Validators.required, CustomeValidator.noSpace]
        subCourse_attach: [null /* , [Validators.required] */],
        mandatoryFor: this.fb.group({
          department: ["", [Validators.required]],
          jobTitle: [
            {
              value: [],
              disabled: true,
            },
            [Validators.required],
          ],
        }),
        recommendedFor: this.fb.group({
          department: ["", [Validators.required]],
          jobTitle: [
            {
              value: [],
              disabled: true,
            },
            [Validators.required],
          ],
        }),
        subjectDescription: [""], //[Validators.required]
        // courseGoal: [""], //[Validators.required]
        // eduCompId: [this.eduCompId],
        from_date: [null], // [Validators.required]
        to_date: [null],
        isArabic: [false, [Validators.required]],
        isEnglish: [false, [Validators.required]],
        isFollow: [false, [Validators.required]],
        courseHours: ["", [Validators.required, CustomeValidator.minusNum]],
        courseMinutes: ["", [Validators.required, CustomeValidator.minusNum]],
        CourseCertificate: [true, [Validators.required]],
      },
      {
        validator: CustomeValidator.dateBefore("from_date", "to_date"),
      } as AbstractControlOptions
    );

    this.FormCtrls.subCourse_attach.valueChanges.subscribe((attach) => {
      if (attach) {
        this.removeImage = 0;
        this.imageChange = true;
      }
    });

    this.mandatoryDeptCtrl.valueChanges.subscribe((mandatoryDeptId) => {
      this.onSelectDepartment(true);
    });
    this.recommendedDeptCtrl.valueChanges.subscribe((recommendedDeptId) => {
      this.onSelectDepartment(false);
    });

    this.getAllCategories();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.eduCompId = e.EduCompId;
      this.getAllCategories();
    });
  }

  getAllCategories() {
    // this.categoriesService.getAllMainCategories().subscribe((res) => {
    //   if (res) this.mainCategories = res.MainCategories;
    // });
  }

  onChaneMainCategory() {
    let mainCategoryId = this.FormCtrls.mainCategoryID.value;
    this.FormCtrls.subCategoryID.setValue(null);
    if (mainCategoryId > 0) {
      this.getAllSubCategories(mainCategoryId);
      this.FormCtrls.subCategoryID.enable();
    } else {
      this.FormCtrls.subCategoryID.disable();
    }
  }

  getAllSubCategories(mainCategoryId: number) {
    // this.spinner.show();
    // this.categoriesService
    //   .getAllSubCategoriesByMainId(mainCategoryId)
    //   .subscribe((res) => {
    //     if (res) this.mainCategories = res;
    //     this.spinner.hide();
    //   });
  }

  onChangeIsFollow(isFollow: boolean) {
    // console.log(isFollow);
    if (isFollow) {
      this.FormCtrls?.Diploma_id?.setValidators([Validators.required]);
      this.FormCtrls?.Diploma_id?.updateValueAndValidity();
    } else {
      this.FormCtrls?.Diploma_id?.clearValidators();
      this.FormCtrls?.Diploma_id?.updateValueAndValidity();
    }
  }

  onChangeLanguage(event: MatCheckboxChange, languageNo: number) {
    if (languageNo == 1) this.FormCtrls.isArabic.setValue(event.checked);
    else if (languageNo == 2) this.FormCtrls.isEnglish.setValue(event.checked);
  }

  onSelectDepartment(mandatory: boolean) {
    // change mandatory
    if (mandatory) {
      // get jobTitles
      this.mandatoryJobCtrl?.setValue("");
      if (this.mandatoryDeptCtrl?.value.length > 0) {
        this.jobServ
          .getSpecificJobTitlesByDepartmentIds({
            ids: this.mandatoryDeptCtrl?.value,
          })
          .subscribe((response: IJobTitleByDeptVM[]) => {
            if (response) this.mandatoryJobTitles = response;
          });
        this.mandatoryJobCtrl?.enable();
      } else {
        this.mandatoryJobCtrl?.disable();
        this.mandatoryJobTitles = [];
      }
    }
    // change recommended
    else {
      // get jobTitles
      this.recommendedJobCtrl?.setValue("");
      if (this.recommendedDeptCtrl?.value.length > 0) {
        this.jobServ
          .getSpecificJobTitlesByDepartmentIds({
            ids: this.recommendedDeptCtrl?.value,
          })
          .subscribe((response: IJobTitleByDeptVM[]) => {
            if (response) this.recommendedJobTitles = response;
          });
        this.recommendedJobCtrl?.enable();
      } else {
        this.recommendedJobCtrl?.disable();
        this.recommendedJobTitles = [];
      }
    }
  }

  addCourse(course: ICourseAddEditVM) {
    // console.log(course);
    this.spinner.show();
    this.courseServ
      .addCourse<ICourseAddEditVM, any>(course)
      .subscribe((res) => {
        if (res?.returnValue == 1) {
          this.msg.success("General.AddSuccessfully");
          this.router.navigate(["dashboard/setup/course/"], {
            queryParams: {
              functionId: this.functionId,
            },
          });
        } else this.msg.error(res?.returnString);
        this.spinner.hide();
      });
  }

  updateCourse(course: ICourseAddEditVM) {
    if (!this.imageChange) course.subCourse_attach = null;
    // console.log(course);
    this.spinner.show();
    this.courseServ
      .updateCourse<ICourseAddEditVM, any>(this.courseId, course)
      .subscribe((res) => {
        if (res?.returnValue == 1) {
          this.msg.success("General.UpdateSuccessfully");
          this.router.navigate([this.backBtnUrl], {
            queryParams: {
              functionId: this.functionId,
            },
          });
        } else this.msg.error(res?.returnString);
        this.spinner.hide();
      });
  }

  onSubmit() {
    // console.log(this.myForm.value);
    this.submitted = true;
    if (!this.FormCtrls?.isArabic.value && !this.FormCtrls.isEnglish.value) {
      this.msg.warning("General.MustChooseLanguage");
      return;
    } else if (!this.myForm.valid) {
      // this.msg.error("course.enterDataMsg");
      this.msg.error("General.FormNotValid");

      // if (!this.FormCtrls?.subCourse_attach.value) {
      //   this.msg.warning("General.MustChoosePicture");
      //   return;
      // }
    } else {
      let course: ICourseAddEditVM = {
        subCourse_ar_name: this.FormCtrls.subCourse_ar_name.value,
        subCourse_en_name: this.FormCtrls.subCourse_en_name.value,
        Diploma_id: this.FormCtrls.Diploma_id.value,
        category_ids: this.FormCtrls.category_ids.value,
        from_date: this.datePipe.transform(
          this.FormCtrls.from_date?.value ?? this.currentDate
        ),
        to_date: this.datePipe.transform(this.FormCtrls.to_date?.value),
        subCourseDepJobTitle: [],
        subCourse_attach: this.FormCtrls.subCourse_attach.value,
        promoLink: this.FormCtrls.subjectPromoVideoPath.value,
        subjectDescription: this.FormCtrls.subjectDescription.value,
        eduCompId: this.eduCompId,
        isArabic: this.FormCtrls.isArabic.value,
        isEnglish: this.FormCtrls.isEnglish.value,
        isFollow: this.FormCtrls.isFollow.value,
        courseHours: this.FormCtrls.courseHours.value,
        courseMinutes: this.FormCtrls.courseMinutes.value,
        courseCertificate: this.FormCtrls.CourseCertificate.value,
        remove_image: this.removeImage,
      };

      let depJodIds: IDeptCourseAddEditVM[] = [];
      this.mandatoryJobCtrl?.value.forEach((depJobId) => {
        depJodIds.push({
          is_mandatory: 1,
          depJobId: depJobId,
        });
      });
      this.recommendedJobCtrl?.value.forEach((depJobId) => {
        depJodIds.push({
          is_mandatory: 0,
          depJobId: depJobId,
        });
      });
      course.subCourseDepJobTitle = depJodIds;
      // console.log(course);

      // add or edit
      if (this.courseId == 0) this.addCourse(course);
      else this.updateCourse(course);
    }
  }

  openAddCategoryDialog() {
    this.dialog
      .open(CategorieComponent, {
        data: {
          showAddOnly: true,
        },
        minWidth: "60vw",
        disableClose: true,
      })
      .afterClosed()
      .subscribe(() => {
        this.getAllCategories();
      });
  }

  openAddDeplomaDialog() {
    this.dialog
      .open(DiplomaComponent, {
        data: {
          showAddOnly: true,
        },
        disableClose: true,
      })
      .afterClosed()
      .subscribe(() => {
        this.reloadDiploma = !this.reloadDiploma;
      });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  get mandatoryDeptCtrl() {
    return this.myForm.get("mandatoryFor.department");
  }

  get mandatoryJobCtrl() {
    return this.myForm.get("mandatoryFor.jobTitle");
  }

  get recommendedDeptCtrl() {
    return this.myForm.get("recommendedFor.department");
  }

  get recommendedJobCtrl() {
    return this.myForm.get("recommendedFor.jobTitle");
  }

  RemoveFile() {
    this.removeImage = 1;
    if (this.course) this.course.attach_path = null;
    this.FormCtrls.subCourse_attach.setValue(null);
  }

  certificateRadioList: RadioButtonModel[] = [
    {
      label: "labels.Yes",
      value: true,
    },
    {
      label: "labels.No",
      value: false,
    },
  ];

  followDiplomaRadioList: RadioButtonModel[] = [
    {
      label: "course.Independent",
      value: false,
    },
    {
      label: "course.InDiploma",
      value: true,
    },
  ];
}
