import { AppAssets } from "app/shared/data/app-assets";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { IJobTitleDatesEditVM } from "app/admin/models/setup/job-title";
import { EditJobTitleDatesComponent } from "app/shared/components/dialogs/edit-job-title-dates/edit-job-title-dates.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import {
  ICourseGetModel,
  ICourseGetVM,
  ICourseJobTitleGetVM,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ISearchCourse } from "app/admin/models/admin/setup/course";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { MessagesService } from "app/shared/services/messages.service";
import { LanguageService } from "app/shared/services/language.service";

@Component({
  selector: "app-course-index",
  templateUrl: "./course-index.component.html",
  styleUrls: ["./course-index.component.scss"],
})
export class CourseIndexComponent implements OnInit {
  EduCompId: number;
  departmentId: number;
  jobId: number;
  totalItems: number;

  displayedColumns: string[] = [
    // "#",
    "attach_path",
    "courseName",
    "category",
    "subCategory",
    "instructor",
    // "department",
    "joinedEmployees",
    "actions",
  ];
  // displayedColumns: string[] = [];

  // lists from api
  // mandatoryColomns: string[] = [];
  // optionalColomns: string[] = [];

  dataSource: MatTableDataSource<ICourseGetVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  myForm!: FormGroup;

  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [
  //   { functionName: "Edit" },
  //   { functionName: "Delete" },
  //   { functionName: "UpdateArticalJob" },
  //   { functionName: "DeleteArticalJob" },
  // ];

  constructor(
    private courseServ: CourseService,
    private fb: FormBuilder,
    private ar: ActivatedRoute,
    public authserv: AuthService,
    private spinner: NgxSpinnerService,
    private msg: MessagesService,
    private dialog: MatDialog,
    public fileService: UploadFileService,
    public languageService: LanguageService,
    public appAssets: AppAssets
  ) {
    ar.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log(`RowFunctions for"${this.functionId}": `, res);
              this.rowFunctions = res;
            }
          });
      }
    });
    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    // console.log(this.eduCompId);
    this.myForm = this.fb.group({
      departmentId: [null],
      jobId: [
        {
          value: null,
          disabled: true,
        },
      ],
      diplomaId: [null],
      courseName: [null],
      page: [1],
    });

    // initialize dataSource | get all courses
    this.getAllCourses(this.page.value);
  }

  // editColumnsToView(index: number, selected: boolean) {
  //   if (selected)
  //     this.displayedColumns.splice(
  //       this.displayedColumns.length - 1,
  //       0,
  //       this.optionalColomns[index]
  //     );
  //   else {
  //     let indexInTable = this.displayedColumns.findIndex(
  //       (ele) => ele == this.optionalColomns[index]
  //     );
  //     this.displayedColumns.splice(indexInTable, 1);
  //   }
  // }
  // selectColumnsToView(values: number[]) {
  //   this.displayedColumns = this.mandatoryColomns.slice();
  //   // values > array of selected index in optional columns
  //   values.forEach((index) =>
  //     this.displayedColumns.splice(
  //       this.displayedColumns.length - 1,
  //       0,
  //       this.optionalColomns[index]
  //     )
  //   );
  // }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllCourses(1);
      this.dataSource.paginator = this.paginator;
    });
  }

  customFilter() {
    this.dataSource.filterPredicate = (data: ICourseGetVM, filter) => {
      return (
        data.subCourse_en_name?.toLocaleLowerCase().indexOf(filter) != -1 ||
        data.subCourse_ar_name?.toLocaleLowerCase().indexOf(filter) != -1 ||
        // data.promoLink?.toLocaleLowerCase().indexOf(filter) != -1 ||
        // data.courseDescreption?.toLocaleLowerCase().indexOf(filter) != -1 ||
        data.department
          ?.map((d) => (this.languageService.isArabic ? d.ar_name : d.en_name))
          .join(" ")
          .toLocaleLowerCase()
          .indexOf(filter) != -1 ||
        data.department
          ?.map((d) =>
            d.jobTitle?.map((j) =>
              this.languageService.isArabic ? j.ar_name : j.en_name
            )
          )
          .join(" ")
          .toLocaleLowerCase()
          .indexOf(filter) != -1
      );
    };
  }

  startSortingTable() {
    // sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "department":
          return this.languageService.isArabic
            ? item.department[0].ar_name ?? item.department[0].en_name
            : item.department[0].en_name ?? item.department[0].ar_name;
        case "jobTitle":
          return this.languageService.isArabic
            ? item.department[0].jobTitle[0]?.ar_name ??
                item.department[0].jobTitle[0]?.en_name
            : item.department[0].jobTitle[0]?.en_name ??
                item.department[0].jobTitle[0]?.ar_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  getAllCourses(p: number) {
    this.page?.setValue(p);

    let searchData: ISearchCourse = {
      depId: this.FormCtrls.departmentId.value || 0,
      jobId: this.FormCtrls.jobId.value || 0,
      // course
      subCourseName:
        this.FormCtrls.courseName.value?.trim().toLowerCase() || null,
      // diploma
      diploma_id: this.FormCtrls.diplomaId.value || 0,
      page: this.page.value,
      eduCompId: this.EduCompId || 0,
    };
    // console.log(searchData);

    this.spinner.show();
    this.courseServ
      .getAllCoursesBySearch<ICourseGetModel>(searchData)
      .subscribe((response) => {
        if (response) {
          let courses = response.subCourseList;
          // put jobTitles of the same department in one array
          courses = courses?.map((course) => {
            let depts = [];
            course.department?.forEach((d) => {
              let index = depts.findIndex((i) => i.id == d.id);
              if (index == -1) depts.push(d);
              else depts[index].jobTitle.push(d.jobTitle[0]);
            });
            course.department = depts;
            return course;
          });
          // console.log(courses);

          this.dataSource = new MatTableDataSource(courses);
          this.totalItems = response.countItems;
          // this.customFilter();
          this.startSortingTable();

          // get optional & mandatory columns
          // this.mandatoryColomns = response.requiredSubjectProperties;
          // this.optionalColomns = response.optionalSubjectProperties;
          // initial table columns
          // if (this.displayedColumns.length == 0)
          //   this.displayedColumns = this.mandatoryColomns.slice();
        }
        this.spinner.hide();
      });
  }

  openDeleteDialog(course: ICourseGetVM): void {
    let courseName = this.languageService.isArabic
      ? course.subCourse_ar_name ?? course.subCourse_en_name
      : course.subCourse_en_name ?? course.subCourse_ar_name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: courseName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeCourse(course.id);
      });
  }

  removeCourse(id: number) {
    this.spinner.show();
    this.courseServ.removeCourse(id).subscribe((res: any) => {
      if (res) {
        this.msg.success("General.DeleteSuccessfully");
        if (this.dataSource.data.length == 1 && this.page.value > 1) {
          // if last item in list: get previous page
          this.getAllCourses(this.page.value - 1);
          this.paginator.pageIndex = this.page.value - 1;
        } else {
          // if not last item in list: get current page
          this.getAllCourses(this.page.value);
        }
      } else this.msg.error(res.returnString);
      this.spinner.hide();
    });
  }

  openUpdateJobTitleDialog(jobTitle: ICourseJobTitleGetVM) {
    // console.log(jobTitle);
    this.dialog
      .open(EditJobTitleDatesComponent, {
        data: {
          jobTitle: jobTitle,
        },
      })
      .afterClosed()
      .subscribe((response) => {
        if (response) {
          // console.log(response.jobTitle);
          this.editJobTitleDates(jobTitle.jobTitleCourseId, response.jobTitle);
        }
      });
  }

  editJobTitleDates(
    jobTitle_articleId: number,
    jobtitle: IJobTitleDatesEditVM
  ) {
    this.spinner.show();
    this.courseServ
      .updateJobTitleCourse<IJobTitleDatesEditVM, any>(
        jobTitle_articleId,
        jobtitle
      )
      .subscribe((response) => {
        if (response.returnValue == 1) {
          this.getAllCourses(this.page.value);
          this.msg.success("General.UpdateSuccessfully");
        } else this.msg.error(response?.returnString);
        this.spinner.hide();
      });
  }

  openDeleteJobtitleDialog(jobTitle: ICourseJobTitleGetVM) {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? jobTitle.ar_name ?? jobTitle.en_name
            : jobTitle.en_name ?? jobTitle.ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeJobtitleArticle(jobTitle.jobTitleCourseId);
      });
  }

  removeJobtitleArticle(jobTitleCourseId: number) {
    this.spinner.show();
    this.courseServ
      .removeJobTitleCourse(jobTitleCourseId)
      .subscribe((response: any) => {
        if (response.returnValue == 1) {
          this.getAllCourses(this.page.value);
          this.msg.success("General.DeleteSuccessfully");
        } else this.msg.error(response?.returnString);
        this.spinner.hide();
      });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }
  get page() {
    return this.myForm.get("page");
  }

  resetSearch() {
    this.myForm.reset();
    this.FormCtrls?.jobId.disable();
    this.getAllCourses(1);
    this.dataSource.paginator = this.paginator;
  }
}
